h4 {
  font-family: "Open Sans";
  letter-spacing: -1px;
  line-height: 22px;
  font-weight: 700;
  color: #2a526b; }

body {
  background-color: #f9f9f9; }

.accordion .dot {
  height: 26px;
  width: 26px;
  border-radius: 50%;
  background-color: #53c6ea;
  display: inline-block;
  color: #2a526b;
  float: left;
  margin: 5px 5px 5px 0; }

.accordion h4 {
  margin: 6px 10px;
  float: left;
  font-family: "Open Sans";
  letter-spacing: -1px;
  line-height: 22px;
  font-weight: 700;
  color: #2a526b; }

.content {
  width: 100% !important; }

.content.open .content-wrapper {
  padding: 10px 40px 40px 40px;
  max-height: 400px; }

.content-wrapper {
  max-height: 0px;
  overflow: hidden;
  -webkit-transition: all .25s ease-in;
  -o-transition: all .25s ease-in;
  transition: all .25s ease-in;
  padding: 0 40px;
  width: 100% !important; }



