h4 {
  font-family: "Open Sans";
  letter-spacing: -1px;
  line-height: 22px;
  font-weight: 700;
  color: #2a526b; }

body {
  background-color: #f9f9f9; }

#mount {
  margin: 100px; }

.dropdown {
  position: relative;
  background: #fafafa;
  border: 1px solid #2a526b;
  border-radius: 1px;
  max-width: 100px;
  color: #2a526b;
  font-family: "Open Sans";
  font-weight: 400;
  margin: 70px 0 20px 0; }
  .dropdown__arrow {
    position: absolute;
    right: -10px;
    top: -1px;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    border-radius: 3px;
    color: #2a526b; }
  .dropdown__list {
    -webkit-transition: max-height .6s ease-out;
    -o-transition: max-height .6s ease-out;
    transition: max-height .6s ease-out;
    max-height: 0;
    overflow: hidden;
    margin: 0;
    padding: 0; }
    .dropdown__list--active {
      max-height: 1000px;
      opacity: 1; }
  .dropdown__list-item {
    border-bottom: 1px solid #eee;
    cursor: pointer;
    list-style: none;
    padding: 2px 10px 2px 10px; }
    .dropdown__list-item--active {
      background: #53c6ea; }
    .dropdown__list-item:hover {
      background: #c1ecf7; }
  .dropdown__toggle {
    background: #fff; }
    .dropdown__toggle:hover {
      background: #53c6ea; }



