.sidebar {
  /* Add shadows to create the "card" effect */
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  margin: 20px;
  padding: 40px 10px; }

/* Add some padding inside the card container */
.sidebar-container {
  padding: 2px 0px; }



