h4 {
  font-family: "Open Sans";
  letter-spacing: -1px;
  line-height: 22px;
  font-weight: 700;
  color: #2a526b; }

body {
  background-color: #f9f9f9; }

.rating-bar {
  width: 110px;
  height: 20px;
  border: 0.5px black solid;
  border-radius: 20px; }

.rating-value {
  background-color: #2a526b;
  border-radius: 18px 0 0 18px;
  width: 20px;
  height: 18px;
  color: #e8f9fc;
  text-align: center;
  float: left; }

.rating-value h5 {
  font-family: 'Open Sans';
  font-size: 12px;
  margin: 0;
  line-height: 18px; }



